::selection {
    background: $highlight;
    /* WebKit/Blink Browsers */
}

::-moz-selection {
    background: $highlight;
    /* Gecko Browsers */
}

// Body

body {
  background: $white;
  color: $black;
}

body::after {
  content: "";
  background: url('/assets/images/textures/marble.jpg');
  opacity: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed;
  z-index: -1;
}
